.workDetail-wrap{
  width: 100%; height: 100vh;
  background-color: rgba($color: #000000, $alpha: 0.8);
  position: fixed;
  top:0; left: 0;
  z-index: 8;
  .pc-close{
    font-size: 3rem;
    position: absolute;
    color: $text-white;
    bottom: 5%; left: 50%;
    transform: translateX(-50px);
    cursor: pointer;
    z-index: 12;
    @include responsive($breakpoint-medium){
      display: none;
    }
  }
  .m-close{
    display: none;
    position: fixed;
    bottom: 0; left: 0;
    width: 100%; height: 4rem;
    background-color: $text-black;
    color: $text-white;
    text-align: center;
    line-height: 4rem;
    z-index: 12;
    cursor: pointer;
    @include responsive($breakpoint-medium){
      display: block;
    }
  }
  .popup{
    width: 80%; max-width: 1170px;
    height: 90%;
    box-sizing: border-box;
    background-color: #fff;
    position: relative;
    top: 45%; left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    @include responsive($breakpoint-medium){
      width: 100%; height: 100vh;
    }
    @include responsive($breakpoint-small){
      padding: 4rem 2rem;
    }    
    .pf-txt{
      h4{
        font-size: $font-size-medium;
        font-weight: 800;
        span{
          color: $primary-color;
        }
      }
      p{
        margin: 1rem 0;
        font-weight: 600;
      }
      ul{
        margin: 2rem 0;
        li{
          list-style: disc;
          margin: 0.5rem 1rem;
        }
      }
    }
  }
  .publishing{
    height: 600px;
    display: flex;
    overflow: hidden;
    img, .pf-txt{
      width: 50%;
    }
    img{
      object-fit: cover;
    }
    .pf-txt{
      padding: 4rem;
    }
  }
  .design{
    height: 80%;
    padding: 4rem;
    overflow-y: scroll;
  }
}

.pf-link{
  text-align: center;
  width: 160px;
  padding: 0.8rem 1rem;
  background-color: $primary-color;
  border: 2px solid $primary-color;
  color: $text-white;
  cursor: pointer;
  &:hover{
    background-color: transparent;
    color: $primary-color;
    font-weight: bold;
  }
}

.pf-img{
  width: 100%;
  padding-top: 2rem;
  img{
    max-width: 100%;
    display: block;
    margin: 0 auto;
  }
}